<section v-hotkey="keymap"><h1 class="title">Key Combination</h1><section class="hero-section"><p>Press <kbd>ctrl</kbd> + <kbd>enter</kbd> to say<b ref="hello">hello.</b></p><p>Press <kbd>alt</kbd> + <kbd>enter</kbd> to say<b ref="bye">bye.</b></p><p>Press <kbd>ctrl</kbd> + <kbd>alt</kbd> + <kbd>enter</kbd> to leave.</p><p :class="{next: true, show: show}">Press <kbd>→</kbd> to play next case.</p></section></section>