<section v-hotkey="keymap"><h1 class="title">Dynamic Keymap With Single Component</h1><section class="hero-section"><p>Press <kbd>tab</kbd> to switch keymap.</p></section><section class="hero-section" v-show="keymapType === 'keymap1'"><p>Press <kbd>ctrl</kbd> + <kbd>enter</kbd> to say<b ref="hello">hello.</b></p><p>You can't say bye now.</p></section><section class="hero-section" v-show="keymapType === 'keymap2'"><p>Press <kbd>alt</kbd> + <kbd>enter</kbd> to say<b ref="bye">bye.</b></p><p>You can't say hello now.</p></section><section class="hero-section"><p :class="{next: true, show: show}">Press <kbd>→</kbd> to play next case.</p></section></section>