<section v-hotkey="keymap"><h1 class="title">Private Hotkeys of Components</h1><section class="hero-section"><p>Press <kbd>tab</kbd> to switch between two components.</p></section><section class="hero-section"><div class="columns"><div class="column is-2"></div><div class="column is-4"><div class="box content component-a" :class="{active: flag}"><h1>Component A</h1><p v-if="flag" v-hotkey="keymapA">Press <kbd>enter</kbd> to say hello.</p><div class="msg" ref="hello">HELLO!</div></div></div><div class="column is-4"><div class="box content component-b" :class="{active: !flag}"><h1>Component B</h1><p v-if="!flag" v-hotkey="keymapB">Press <kbd>enter</kbd> to say bye.</p><div class="msg" ref="bye">BYE!</div></div></div><div class="column is-2"></div></div></section><section class="hero-section"><p :class="{next: true, show: show}">Press <kbd>→</kbd> to play next case.</p></section></section>